export default {
	data() {
		return {
			content:'',
			contact:''
		};
	},
	methods:{
		feedback(){
			this.$uniRequest(
				this.$baseUrl+'/v1/member/feedback',
				{
					content:this.content,
					contact:this.contact
				},
				(res)=>{
					if(res.data.code == 200){
						uni.showToast({
							title:res.data.msg,
							mask:true,
							success:()=>{
								setTimeout(()=>{
									uni.navigateBack()
								},1000)
							}
						})
					}else{
						uni.showToast({
							title:'内容不能为空',
							icon:'none'
							
						})
					}
				},
				false
			)
		}
	}
}
